module.exports = "<ng-template #modalContent let-close=\"close\">\n    <div class=\"modal-header\">\n        <h5 class=\"modal-title\">Event action occurred</h5>\n        <button type=\"button\" class=\"close\" (click)=\"close()\">\n            <span aria-hidden=\"true\">&times;</span>\n        </button>\n    </div>\n    <div class=\"modal-body\">\n        <div>\n            Action:\n            <pre>{{ modalData?.action }}</pre>\n        </div>\n        <div>\n            Event:\n            <pre>{{ modalData?.event | json }}</pre>\n        </div>\n    </div>\n    <div class=\"modal-footer\">\n        <button type=\"button\" class=\"btn btn-secondary\" (click)=\"close()\">OK</button>\n    </div>\n</ng-template>\n\n<div class=\"container-fluid fill-height\">\n    <div class=\"profile-banner-bg\"></div>\n    <div class=\"col-md-12 banner-text\">\n        <h3> Conference schedule - [Conference] </h3>\n    </div>\n    <div class=\"col-md-12 no-pad\">\n        <md-card>\n            <div class=\"description-field\">\n                <p>Add all scheduled conference events here, attendees will use this information to plan their day and attend events.</p>\n            </div>\n        </md-card>\n    </div>\n    <div class=\"col-md-12 no-pad table-view\">\n        <md-card>\n            <div class=\"col-md-4\">\n                <div class=\"btn btn-default\"\n                    mwlCalendarPreviousView\n                    [view]=\"view\"\n                    [(viewDate)]=\"viewDate\">\n                    Previous\n                </div>\n                <div class=\"btn btn-default\"\n                    mwlCalendarToday\n                    [(viewDate)]=\"viewDate\">\n                    Today\n                </div>\n                <div class=\"btn btn-default\"\n                    mwlCalendarNextView\n                    [view]=\"view\"\n                    [(viewDate)]=\"viewDate\">\n                    Next\n                </div>\n            </div>\n            <div class=\"col-md-4\">\n                <h3>{{ viewDate | calendarDate:(view + 'ViewTitle'):'en' }}</h3>\n            </div>\n            <div class=\"col-md-4\">\n                <div class=\"btn-group\">\n                    <div\n                        class=\"btn btn-primary\"\n                        (click)=\"view = 'week'\"\n                        [class.active]=\"view === 'week'\">\n                        Week\n                    </div>\n                    <div\n                        class=\"btn btn-primary\"\n                        (click)=\"view = 'day'\"\n                        [class.active]=\"view === 'day'\">\n                        Day\n                    </div>\n                </div>\n            </div>\n            <div class=\"col-md-12 line-break\"></div>\n            <br>\n            <div [ngSwitch]=\"view\">\n                <mwl-calendar-week-view\n                    *ngSwitchCase=\"'week'\"\n                    [viewDate]=\"viewDate\"\n                    [events]=\"events\"\n                    [refresh]=\"refresh\"\n                    (eventClicked)=\"handleEvent('Clicked', $event.event)\"\n                    (eventTimesChanged)=\"eventTimesChanged($event)\">\n                </mwl-calendar-week-view>\n                <mwl-calendar-day-view\n                    *ngSwitchCase=\"'day'\"\n                    [viewDate]=\"viewDate\"\n                    [events]=\"events\"\n                    [refresh]=\"refresh\"\n                    (eventClicked)=\"handleEvent('Clicked', $event.event)\"\n                    (eventTimesChanged)=\"eventTimesChanged($event)\">\n                </mwl-calendar-day-view>\n            </div>\n\n            <div class=\"col-md-12 line-break\"></div>\n\n            <h3 id=\"edit-event-label\" (click)=\"showEdit=!showEdit\">\n                Edit events <i class=\"fa fa-chevron-down\" aria-hidden=\"true\"></i>\n            </h3>\n            <button\n                md-button color=\"primary\"\n                class=\"pull-right add-new-button\"\n                (click)=\"addEvent()\">\n                Add new\n            </button>\n\n            <table class=\"event-editor-table\" *ngIf=\"showEdit\" [@enterAnimation]=\"showEdit\">\n\n                <thead>\n                    <tr>\n                    <th>Title</th>\n                    <th>Color</th>\n                    <th>Starts at</th>\n                    <th>Ends at</th>\n                    <th></th>\n                    </tr>\n                </thead>\n\n                <tbody>\n                    <tr *ngFor=\"let event of events; let index = index\">\n                    <td class=\"title-col\">\n                        <md-input-container>\n                            <input\n                            mdInput\n                            [(ngModel)]=\"event.title\"\n                            (keyup)=\"refresh.next()\">\n                        </md-input-container>\n                    </td>\n                    <td class=\"scolor-col\">\n                        <input\n                        type=\"color\"\n                        [(ngModel)]=\"event.color.secondary\"\n                        (change)=\"refresh.next()\">\n                    </td>\n                    <td class=\"btime-col\">\n                        <div class=\"lets-see\">\n                        <mwl-utils-date-time-picker\n                        [(date)]=\"event.start\"\n                        (dateChange)=\"refresh.next()\"\n                        placeholder=\"Not set\">\n                        </mwl-utils-date-time-picker>\n                        </div>\n                    </td>\n                    <td class=\"etime-col\">\n                        <mwl-utils-date-time-picker\n                        [(date)]=\"event.end\"\n                        (dateChange)=\"refresh.next()\"\n                        placeholder=\"Not set\">\n                        </mwl-utils-date-time-picker>\n                    </td>\n                    <td class=\"del-col\">\n                        <i class=\"fa fa-trash\" (click)=\"events.splice(index, 1); refresh.next()\" aria-hidden=\"true\">\n                        </i>\n                    </td>\n                    </tr>\n                </tbody>\n\n            </table>\n        </md-card>\n    </div>\n</div>"

